import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, LoadingController, AlertController} from 'ionic-angular';
import {Http} from '@angular/http';
import 'rxjs/add/operator/map';
import {AppConfig} from '../../../../app/app.config';
/**
 * Generated class for the BindaccoundPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-bindaccound',
    templateUrl: 'bindaccound.html',
})
export class BindaccoundPage {
    url1: string = AppConfig.getUrl() + "/user/bindDJY";  //党建云
    url2: string = AppConfig.getUrl() + "/user/bindXFW";  //先锋网
    user: any = {loginname: "", pwd: "",userid: localStorage.userid}
    id = this.navParams.data.id;

    constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public loadingCtrl: LoadingController, public alertCtrl: AlertController) {
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad BindaccoundPage');
    }

    back() {
        var url;
        if(this.id ==1){
            url = this.url1
        }else {
            url = this.url2
        }
        console.log(url)
        let loading = this.loadingCtrl.create({
            content: '加载中...'
        });
        loading.present();
        return new Promise((resolve, reject) => {
            this.http.post(url, null, {params: this.user})
                .map(res => res.json())
                .subscribe(data => {
                    loading.dismiss();
                    resolve(data);
                    console.log(data)
                    if (data.status) {
                        if (this.id ==1) {
                            localStorage.setItem('djycookie', data.cookie);
                            localStorage.setItem('djyuserName', data.userName);
                            localStorage.setItem('djydanwei', data.danwei);
                        }else{
                            localStorage.setItem('xfwcookie', data.cookie);
                        }
                        let alert = this.alertCtrl.create({
                            title: '绑定成功!'
                        });
                        alert.present();
                        this.navCtrl.popToRoot()
                    }else{
                        let alert = this.alertCtrl.create({
                            title: data.msg
                        });
                        alert.present();
                    }
                }, err => {
                    reject(err)
                    loading.dismiss();
                    console.log(err);
                    let alert = this.alertCtrl.create({
                        title: '服务器连接失败!'
                    });
                    alert.present();
                })
        })
    }
}



